import ContextMenu from './components/ContextMenu'

export const install = (Vue, constructorOptions) => {
  // è·åæä»¶éé¡¹
  const globalOptions = Object.assign({}, {
    menuListClass: 'vue-right-click-list',
    menuItemClass: 'vue-right-click-item'
  }, constructorOptions)

  Vue.directive('right-click', {
    /**
     * ææ¡£å°åï¼https://cn.vuejs.org/v2/guide/custom-directive.html#%E9%92%A9%E5%AD%90%E5%87%BD%E6%95%B0
     * å ä¸ºå³å»èååºç°æ¯éå¿è¦åç´ ï¼è¿è¾¹ä½¿ç¨ inserted é©å­å½æ°ï¼æé«æä»¶æ§è½
     * @param el æä»¤æç»å®çåç´ ï¼å¯ä»¥ç¨æ¥ç´æ¥æä½ DOM
     * @param binding ç»å®åç´ å±æ§å¯¹è±¡éå
     * @param vnode Vue ç¼è¯çæçèæèç¹
     * @param oldVnode ä¸ä¸ä¸ªèæèç¹ï¼ä»å¨ update å componentUpdated é©å­ä¸­å¯ç¨
     */
    inserted: (el, binding, vnode, oldVnode) => {
      /**
       * çå¬é¼ æ å³é®
       * @param event é¼ æ åçäºä»¶
       */
      el.oncontextmenu = event => {
        // é»æ­¢äºä»¶
        event.preventDefault()
        if (!vnode.context['hasContextMenu']) {
          // èµå¼ç¶æ
          vnode.context['hasContextMenu'] = true
          // çæå³å»èåçé¢
          const ContextMenuNode = Vue.extend(ContextMenu)
          const node = el.appendChild(document.createElement('div'))
          // è®¡ç®ç¹å»ä½ç½®
          // todo èªéåºå¼¹çªä½ç½®
          const rightClickHeaderRowLeft = event.offsetX
          const rightClickHeaderRowTop = event.offsetY

          // è°ç¨ç»ä»¶
          const contextMenuNode = new ContextMenuNode({
            propsData: {
              visible: true,
              contextMenuTop: rightClickHeaderRowTop,
              contextMenuLeft: rightClickHeaderRowLeft,
              menuList: typeof binding.value === 'function' ? binding.value() : binding.value,
              pluginOptions: globalOptions
            }
          }).$mount(node)

          // çå¬ç»ä»¶è§¦åäºä»¶æ§è¡åè°äºä»¶
          contextMenuNode.$on('close-menu', value => {
            // çå¬å³é­å¹¶ä¸èµå¼
            vnode.context['hasContextMenu'] = value
          })
        }
      }
    }
  })
}

if (window.Vue) window.Vue.use(install)

export default install
